import { apiUrl } from "./config.js";
import { getUserInfo } from "./localStorage.js";
import { showMessage } from "./utils.js";

export const getProduct = async (id) => {
  try {
    const res = await fetch(`${apiUrl}/api/product/${id}`);
    const data = await res.json();
    if (!res.ok) throw new Error(`${data.message} (${res.status})`);
    return data;
  } catch (err) {
    console.log(err);
  }
};

export const signin = async ({ email, password }) => {
  try {
    const res = await fetch(`${apiUrl}/signin`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: email,
        password: password,
      }),
    });
    const data = await res.json();

    if (password === "") {
      showMessage("âš ï¸IntroduceÈ›i o parolÄƒ!");
    } else if (email === "") {
      showMessage("âš ï¸IntroduceÈ›i un e-mail!");
    }

    if (!res.ok) throw new Error(`${data.message} (${res.status})`);
    return data;
  } catch (err) {
    showMessage("âš ï¸Conectarea nu a reuÈ™it");
  }
};

export const register = async ({ name, email, password }) => {
  try {
    const res = await fetch(`${apiUrl}/register`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name: name,
        email: email,
        password: password,
      }),
    });
    const data = await res.json();

    if (password === "") {
      showMessage("âš ï¸IntroduceÈ›i o parolÄƒ!");
    } else if (email === "") {
      showMessage("âš ï¸IntroduceÈ›i un e-mail!");
    } else if (name === "") {
      showMessage("âš ï¸IntroduceÈ›i un nume!");
    }
    return data;
  } catch (err) {
    showMessage("âš ï¸Inregistrarea nu a reuÈ™it");
  }
};

export const update = async ({ name, email, password }) => {
  try {
    const { id } = getUserInfo();
    const res = await fetch(`${apiUrl}/profile/${id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name: name,
        email: email,
        password: password,
      }),
    });
    const data = await res.json();
    return data;
  } catch (err) {
    console.log(err);
  }
};

export const createOrder = async (order) => {
  try {
    const res = await fetch(`${apiUrl}/api/order`, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(order),
    });
    const data = await res.json();
    console.log(data);
    if (res.ok) showMessage("âœŒï¸Comanda a fost plasatÄƒ!");
    return data;
  } catch (err) {
    return { error: err.res ? err.res.data.message : err.message };
  }
};
